import React from 'react'
import { Button } from '@material-ui/core'
const Save = (props) => {
  const { variant, size, color, disabled,handleClose={}, text, type } = props;
  return (
    <>
      <Button
        style={{ margin: "17px" }}
        variant={variant}
        size={size}
        color={color}
        type={type}
        disabled={disabled}
        // onClick={handleClose}
      >
        {text}
      </Button>
    </>
  );
};

export default Save