import React from 'react'
import { Button } from '@material-ui/core';
const ResetButton = (props) => {
  const { variant, size, color, disableElevation, text, disabled, onClick } =
    props;

  return (
    <>
      <Button
        style={{ margin: "17px" }}
        variant={variant}
        size={size}
        color={color}
        disableElevation={disableElevation}
        disabled={disabled}
        onClick={onClick}
      >
        {text}
      </Button>
    </>
  );
};

export default ResetButton