import React, { useState } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  Stack,
  TextField,
  Typography,
} from "@material-ui/core";
import toast from "react-hot-toast";
import axios from "axios";
import { Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import Design from "../Design";
import { Navigate, useLocation, useNavigate } from "react-router";
import { MobileDatePicker } from "@material-ui/lab";
import { useEffect } from "react";
import customAxios from "../apis/Api";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  feild: {
    width: "30%",
    marginLeft: 5,
  },
  head: {
    height: "70vh",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
    background: "none",
  },
}));

const pay = [
  {
    id: 1,
    value: "Bank",
  },
  {
    id: 2,
    value: "Cash",
  },
];
export default function ExpenseCreateForm() {
  const classes = useStyles();
  const { state } = useLocation();
  console.log("state", state);
  const [checked, setChecked] = useState(false);
  const [data, setData] = useState([]);
  const [type, setType] = useState("");

  if (state === null) {
    var row = {
      name: "",
      date: "",
      payMethod: "",
      expenseHead: "",
      description: "",
      amount: "",
      teacherName: "",
    };
  } else {
    state.row;
    var { row } = state;
  }
  const staffData = async () => {
    const Data = await customAxios().get("teacher/teachers/api");
    setData(Data.data);
  };
  const handleChanges = (event, row) => {
    const isChecked = event.target.checked;

    if (!isChecked) {
      let Data = 0;
      setData(Data);
      setType("");
      // console.log("⛔️ Checkbox is NOT checked");
    } else {
      // console.log("✅ Checkbox is checked");

      staffData();
    }
    setChecked((current) => !current);
  };

  const hanldeChanged = (e) => {
    setType(e.target.value);
  };

  console.log(parseInt(row.name));
  return (
    <>
      <Card className={classes.head}>
        <Formik
          enableReinitialize
          initialValues={{
            name: row.name || type,
            date: row.date || new Date().toLocaleDateString(),
            paymethod: row.payMethod || "",
            expenseHead: row.expenseHead || "",
            description: row.description || "",
            amount: row.amount || "",
            // status:data.length > 0 ? 1 : 0,
          }}
          validationSchema={Yup.object().shape({
            payMethod:Yup.number()
          })}
          onSubmit={async (values) => {
            await new Promise((r) => setTimeout(r, 500));
            let data = { ...values, id: row.id ? row.id : "" };
            await customAxios()
              .post("finance/expresneList", {
                ...data,
              })
              .then((res) => {
                console.log(res);
              });
              toast.success(row.id ? " Update Data " : "Add SuccessFully");

          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            dirty,
            setFieldValue,
            setFieldTouched,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              <form noValidate onSubmit={handleSubmit}>
                <div>
                  <FormControl
                    style={{ paddingLeft: 10, marginTop: 10, marginRight: 30 }}
                    component="fieldset"
                  >
                    <FormGroup>
                      <FormControlLabel
                        checked={
                          checked ||
                          parseInt(row.name) == "NaN"
                            ? setChecked((checked) => checked)
                            : setChecked((checked) => !checked)
                        }
                        onClick={handleChanges}
                        control={<Checkbox />}
                        label="Staff"
                      />
                    </FormGroup>
                  </FormControl>
                  {data.length > 0 ? (
                    <FormControl className={classes.feild}>
                      <TextField
                        fullWidth
                        labelId="name"
                        select
                        variant="outlined"
                        SelectProps={{ native: true }}
                        value={values.name}
                        onFocus={handleChange}
                        onChange={handleChange}
                        id="name"
                        label="name"
                        name="name"
                      >
                        {data.map((option) => (
                          <option key={option.id} value={option.id}>
                            {option.first_name}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  ) : (
                    <TextField
                      sx={{ width: "32%" }}
                      autoComplete="off"
                      id="name"
                      label="Enter Name"
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.name && errors.name)}
                      helperText={touched.name && errors.name}
                      onBlur={handleBlur}
                      onChange={(e) => {
                        handleChange(e), hanldeChanged(e);
                      }}
                      value={type||row.name}
                    />
                  )}
                </div>

                <Stack spacing={3}>
                  <div
                    style={{
                      display: "flex",
                      alignItem: "end",
                      justifyContent: "end",
                      flexWrap: "wrap",
                    }}
                  >
                    <Box
                      sx={{
                        width: "30%",
                        mt: 4,
                      }}
                    >
                      <Box sx={{ ml: 1.2 }}>
                        <MobileDatePicker
                          fullwidth
                          label="Date"
                          onAccept={() => setFieldTouched("date")}
                          onChange={(date) =>
                            setFieldValue("date", date.toLocaleDateString())
                          }
                          inputFormat="yyyy/MM/dd"
                          onClose={() => setFieldTouched("date")}
                          renderInput={(inputProps) => (
                            <TextField variant="outlined" {...inputProps} />
                          )}
                          value={values.date}
                        />
                      </Box>
                    </Box>
                    {Boolean(touched.date && errors.date) && (
                      <Box sx={{ mt: 2 }}>
                        <FormHelperText error>{errors.date}</FormHelperText>
                      </Box>
                    )}
                  </div>
                  <div
                    style={{
                      display: "flex",
                      alignItem: "end",
                      justifyContent: "end",
                      flexWrap: "wrap",
                    }}
                  >
                    <FormControl className={classes.feild}>
                      <TextField
                        fullWidth
                        labelId="paymethod"
                        select
                        variant="outlined"
                        SelectProps={{ native: true }}
                        value={values.paymethod}
                        onFocus={handleChange}
                        onChange={handleChange}
                        id="paymethod"
                        label="Cash"
                        name="paymethod"
                      >
                        {pay.map((option) => (
                          <option key={option.id} value={option.id}>
                            {option.value}
                          </option>
                        ))}
                      </TextField>
                    </FormControl>
                  </div>
                </Stack>
                <Box>
                  <Stack sx={{ my: 3 }} direction="row" spacing={2}>
                    <TextField
                      sx={{ width: "34%" }}
                      autoComplete="off"
                      id="expenseHead"
                      label="ExpenseHead"
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.expenseHead && errors.expenseHead)}
                      helperText={touched.expenseHead && errors.expenseHead}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.expenseHead}
                    />
                    <TextField
                      sx={{ width: "34%" }}
                      autoComplete="off"
                      id="description"
                      label="Description"
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.description && errors.description)}
                      helperText={touched.description && errors.description}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.description}
                    />
                    <TextField
                      sx={{ width: "32%" }}
                      autoComplete="off"
                      id="amount"
                      label="amount"
                      variant="outlined"
                      type="number"
                      error={Boolean(touched.amount && errors.amount)}
                      helperText={touched.amount && errors.amount}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.amount}
                    />
                  </Stack>
                </Box>
                <Save
                  text={"Save "}
                  variant="contained"
                  size="medium"
                  color="success"
                  type="submit"
                  disabled={isSubmitting}
                />

                <ResetButton
                  text="Reset"
                  variant="contained"
                  size="medium"
                  color="primary"
                  type="button"
                  onClick={handleReset}
                  disabled={!dirty || isSubmitting}
                />
              </form>
            </div>
          )}
        </Formik>
      </Card>
    </>
  );
}
