import react from "react";

import {
    Box,
    Breadcrumbs,
    Button,
    Grid,
    Typography,
    Link,
} from "@material-ui/core";
import { Link as RouterLink } from "react-router-dom";
import ChevronRightIcon from "../../icons/ChevronRight";
import UploadIcon from "../../icons/Upload";
import DownloadIcon from "../../icons/Download";


const Head = (props) => {
    const { finance, expense, incomeList, ImportData, balanceSta } = props
    return (
        <Grid item>
            <Typography color="textPrimary" variant="h5">
                {finance}
            </Typography>
            <Breadcrumbs
                aria-label="breadcrumb"
                separator={<ChevronRightIcon fontSize="small" />}
                sx={{ mt: 1 }}
            >
                <Link
                    color="textPrimary"
                    component={RouterLink}
                    to="/dashboard"
                    variant="subtitle2"
                >
                    Dashboard
                </Link>
                <Link
                    color="textPrimary"
                    component={RouterLink}
                    to="/dashboard/IncomeList"
                    variant="subtitle2"
                >
                    {incomeList}
                </Link>
                <Link
                    color="textPrimary"
                    component={RouterLink}
                    to="/dashboard/expenseList"
                    variant="subtitle2"
                >
                    {expense}
                </Link>
                <Link
                    color="textPrimary"
                    component={RouterLink}
                    to="/dashboard/Income-statment"
                    variant="subtitle2"
                >
                    {balanceSta}
                </Link>
            </Breadcrumbs>
            <Box
                sx={{
                    mb: -1,
                    mx: -1,
                    mt: 1,
                }}
            >
                <Button
                    onClick={ImportData}
                    color="primary"
                    startIcon={<UploadIcon fontSize="small" />}
                    sx={{ m: 1 }}
                >
                    Import
                </Button>

                <Button
                    color="primary"
                    startIcon={<DownloadIcon fontSize="small" />}
                    sx={{ m: 1 }}
                >
                    Export
                </Button>
            </Box>
        </Grid>
    )
}

export default Head